"use client";

import { useState } from "react";
import { useRouter, useSearchParams } from "next/navigation";
import { Heebo } from "next/font/google";

const heebo = Heebo({ subsets: ["hebrew"] });

export default function GeneratorLogic() {
    const router = useRouter();
    const searchParams = useSearchParams();

    const [offer, setOffer] = useState("");        //  注住拽 爪注
    const [audience, setAudience] = useState("");  // 拽 注
    const [usp, setUsp] = useState("");            //  
    const [goal, setGoal] = useState("");     // 专
    const [style, setStyle] = useState("专");  // 住 注爪

    const handleSubmit = (e) => {
        e.preventDefault();

        const prompt = `
转  祝 转 拽爪注 注住拽 砖专.
 驻专 注住拽:

 注住拽 爪注: ${offer}
拽 注: ${audience}
  转 注住拽: ${usp}
专转 祝: ${goal}
住 注爪: ${style}

爪专 JSON  注:
- title
- subtitle
- sections[] ( 住拽砖 注 title + text)
- cta_text
- cta_button
- color_theme
- tone_of_voice

祝 爪专 转 注专转, 专, 拽爪注 专 砖 砖拽 .
        `;

        router.push(`/result?p=${encodeURIComponent(prompt)}`);
    };

    const inputStyle = {
        width: "100%",
        padding: "15px",
        borderRadius: "12px",
        border: "1px solid rgba(255,255,255,0.2)",
        background: "rgba(255,255,255,0.05)",
        color: "white",
        marginTop: "8px",
        fontSize: "1rem",
        outline: "none"
    };

    return (
        <div className={heebo.className} dir="rtl" style={{
            minHeight: "100vh",
            background: "#05070a",
            color: "white",
            padding: "40px 20px"
        }}>
            <h1 style={{
                fontSize: "2.5rem",
                fontWeight: "900",
                textAlign: "center",
                marginBottom: "40px",
                background: "linear-gradient(135deg, #60a5fa, #c084fc)",
                WebkitBackgroundClip: "text",
                WebkitTextFillColor: "transparent"
            }}>
                爪专转 祝 转 砖 
            </h1>

            {/* Fast Track Skip Button (if description is present) */}
            {searchParams.get('p') || searchParams.get('description') ? (
                <button
                    onClick={() => router.push(`/result?p=${encodeURIComponent(searchParams.get('p') || searchParams.get('description'))}`)}
                    style={{
                        background: 'none', border: 'none', color: '#60a5fa',
                        textDecoration: 'underline', cursor: 'pointer',
                        fontSize: '1rem', margin: '0 auto', display: 'block', marginBottom: '20px'
                    }}
                >
                     砖  专 转专,  砖专 爪专
                </button>
            ) : null}

            <form onSubmit={handleSubmit} style={{
                maxWidth: "700px",
                margin: "0 auto",
                display: "flex",
                flexDirection: "column",
                gap: "25px"
            }}>

                {/*  注住拽 爪注 */}
                <div>
                    <label style={{ fontWeight: "600" }}> 注住拽 爪注?</label>
                    <input
                        type="text"
                        value={offer}
                        onChange={(e) => setOffer(e.target.value)}
                        placeholder=":  砖专 砖 / 砖专转 砖转 24/7"
                        style={inputStyle}
                        required
                    />
                </div>

                {/* 拽 注 */}
                <div>
                    <label style={{ fontWeight: "600" }}> 拽 注?</label>
                    <input
                        type="text"
                        value={audience}
                        onChange={(e) => setAudience(e.target.value)}
                        placeholder=": 砖 专  / 注 专转 / 注住拽 拽"
                        style={inputStyle}
                        required
                    />
                </div>

                {/* USP */}
                <div>
                    <label style={{ fontWeight: "600" }}>  转 注住拽?</label>
                    <input
                        type="text"
                        value={usp}
                        onChange={(e) => setUsp(e.target.value)}
                        placeholder=": 住 砖 10 砖 / 砖专转 专 / 转爪转 转"
                        style={inputStyle}
                        required
                    />
                </div>

                {/* 专 */}
                <div>
                    <label style={{ fontWeight: "600" }}> 专 砖 祝?</label>
                    <select
                        value={goal}
                        onChange={(e) => setGoal(e.target.value)}
                        style={inputStyle}
                    >
                        <option></option>
                        <option>专</option>
                        <option>拽注转 砖转 注抓</option>
                        <option>爪转 注</option>
                    </select>
                </div>

                {/* 住 注爪 */}
                <div>
                    <label style={{ fontWeight: "600" }}>住 注爪</label>
                    <select
                        value={style}
                        onChange={(e) => setStyle(e.target.value)}
                        style={inputStyle}
                    >
                        <option>专</option>
                        <option>拽专转</option>
                        <option>爪注专</option>
                        <option>拽</option>
                    </select>
                </div>

                <button type="submit" style={{
                    padding: "18px",
                    background: "linear-gradient(135deg, #2563eb, #7c3aed)",
                    border: "none",
                    borderRadius: "12px",
                    fontWeight: "bold",
                    fontSize: "1.2rem",
                    cursor: "pointer"
                }}>
                    爪专 祝 转 
                </button>

                <button
                    type="button"
                    onClick={() => router.push('/result?p=TEST_SIMULATION')}
                    style={{
                        padding: "12px",
                        background: "rgba(255,255,255,0.1)",
                        border: "1px dashed rgba(255,255,255,0.3)",
                        borderRadius: "12px",
                        color: "rgba(255,255,255,0.7)",
                        fontSize: "0.9rem",
                        cursor: "pointer"
                    }}
                >
                    И 专抓 住爪 (拽转 注专转)
                </button>
            </form>
        </div>
    );
}
